import { win } from "win-doc";
var stylesStore;
var oWin = win();
var g = oWin ? oWin : global;

if (g.reactStylesStore) {
  stylesStore = g.reactStylesStore;
} else {
  stylesStore = g.reactStylesStore = {
    registry: [],
    newStyles: [],
    mods: [],
    // needCss
    counter: 0
  };
}

export default stylesStore;