import React, { useEffect, useRef } from "react";
import { popupDispatch } from "../../src/popupDispatcher";

var DisplayPopupEl = function DisplayPopupEl(props) {
  var _mount = useRef(false);

  var popup = useRef(props.children);
  useEffect(function () {
    _mount.current = true;
    return function () {
      _mount.current = false;
      popupDispatch({
        type: "dom/cleanOne",
        params: {
          popup: popup.current
        }
      });
    };
  }, []);
  useEffect(function () {
    popup.current = props.children;

    if (_mount.current) {
      popupDispatch({
        type: "dom/update",
        params: {
          popup: popup.current
        }
      });
    }
  }, [props.children]);
  return null;
};

export default DisplayPopupEl;