import get from "get-object-value";
var shareAssets = {
  "grapes.min.css": "https://cdn.jsdelivr.net/npm/grapesjs@0.16.27/dist/css/grapes.min.css",
  "grapes.min.js": "https://cdn.jsdelivr.net/npm/grapesjs@0.16.27/dist/grapes.min.js",
  // ckeditor can not use ckeditor.min.js
  "ckeditor.js": "https://cdn.jsdelivr.net/npm/ckeditor4@4.15.0/ckeditor.js",
  "mjml.js": "https://cdn.jsdelivr.net/npm/organism-react-grapesjs@0.5.6/dist/mjml.js" //  "http://localhost:3001/assets/mjml.bundle.js",
  //  "http://localhost:3001/dist/mjml.js",

};

var getAsset = function getAsset(fileName, props, defaultAssets) {
  var assetPath = props.assetPath,
      assets = props.assets;

  if (assetPath) {
    return assetPath + fileName;
  } else if (get(assets, [fileName])) {
    return assets[fileName];
  } else {
    return defaultAssets[fileName] || shareAssets[fileName];
  }
};

export default getAsset;